<template>
	<div id="single-blog">
		<h1>{{blog.title}}</h1>
		<article>{{blog.content}}</article>
		<p>作者：{{blog.author}}</p>
		<p>分类：</p>
		<ul>
			<li v-for = "category in blog.categories" :key="category">
				{{category}}
			</li>
		</ul>
		<button v-on:click="deleteSingleBlog()">删除</button>
		<router-link v-bind:to = "'/edit/' + id">编辑</router-link>
	</div>
</template>

<script>
import axios from 'axios'
	export default{
		name:"single-blog",
		data(){
			return{
				id:this.$route.params.id,
				blog:{},
			}
		},
		created(){
			// this.$http.get("https://wd2496636449sqjclp.wilddogio.com/posts/"+this.id + ".json")
			axios.get("/posts/"+this.id + ".json").then((data)=>{
				// console.log(data.data);
				this.blog = data.data;
			})
		},
		methods:{
			deleteSingleBlog:function(){
				axios.delete("/posts/"+this.id + ".json").then((response)=>{
					// console.log(this.$router);
					this.$router.push("/");
				})
			}
		}
	}
</script>

<style>
	#single-blog{
		max-width: 960px;
		margin:0 auto;
		padding:20px;
		background: #eee;
		border: 1px dotted #aaa; 
	}
</style>


